import Vue from "vue";
import VueRouter from "vue-router";
import ControlCamaras from "../views/ControlCamaras.vue";
import TiempoReal from "../views/TiempoReal.vue";

Vue.use(VueRouter);

const routes = [{
        path: "/",
        name: "TiempoReal",
        component: TiempoReal
    },
    {
        path: "/control-camaras",
        name: "ControlCamaras",
        component: ControlCamaras
    }
];

const router = new VueRouter({
    mode: "history",
    base: process.env.BASE_URL,
    routes
});

export default router;